<template>
  <div>
    <analysis-header @refresh="refresh" @interval="interval"></analysis-header>
    <a-divider></a-divider>
    <div id="base">
      <a-row>
        <a-col :span="24" class="col">
          <div class="chart" id="heatmap"></div>
        </a-col>
      </a-row>
      <a-row>
        <a-col :span="24" class="col">
          <div class="chart" id="multi-pie"></div>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import { getMultiPie, getHeatmap } from "network/analysis";
import AnalysisHeader from "./childComp/AnalysisHeader";

export default {
  name: "HouseSource",
  data() {
    return {
      multiPie: "",
      heatmap: "",
      isInterval: false
    };
  },
  components: {
    AnalysisHeader
  },
  mounted() {
    this.multiPie = echarts.init(
      document.getElementById("multi-pie"),
      "white",
      {
        renderer: "canvas"
      }
    );
    this.heatmap = echarts.init(document.getElementById("heatmap"), "white", {
      renderer: "canvas"
    });
    this.$nextTick(() => {
      this.getEchartBar();
    });
  },
  methods: {
    getEchartBar() {
      getMultiPie().then(res => {
        this.multiPie.setOption(res.data.data);
      });
      getHeatmap().then(res => {
        this.heatmap.setOption(res.data.data);
      });
    },
    chartInterval() {
      if (!this.isInterval) {
        return;
      }
      this.getEchartBar();
      setTimeout(this.chartInterval, 5000);
    },
    /**
     * 刷新与定时器
     */
    refresh() {
      this.getEchartBar();
    },
    interval(isInterval) {
      this.isInterval = isInterval;
      this.chartInterval();
    }
  }
};
</script>

<style scoped>
#base {
  height: 600px;
  overflow: hidden;
  overflow-y: scroll;
}
.chart {
  width: 85%;
  margin: auto;
  left: initial;
  height: 65vh;
}
#multi-pie {
  margin-top: 40px;
  margin-bottom: 20px;
}
#heatmap {
  margin-top: 0;
  height: 70vh;
  box-shadow: 60px 16px 15px 5px #6a8eb0;
  border: 1em ridge rgb(85, 137, 214);
  border-radius: 5px;
}
</style>